<template>
  <div>
    <a-card title="小马哥定价工具 微信Ma995234692   感谢信任">
      <div class="numberContainer">
        <div class="input">
          <div>
            成本：
            <a-input-number
              id="inputNumber"
              v-model:value="valueCost"
              :min="0"
              :max="Infinity"
              size="large"
              :precision="2"
            />
          </div>
          <div>
            售价：
            <a-input-number
              id="inputNumber"
              v-model:value="valuePrice"
              :min="0"
              :max="Infinity"
              size="large"
              :precision="2"
              @change="comPuteProfit"
            />
          </div>
          <div>
            佣金：
            <a-input-number
              v-model:value="valueProfitInitial"
              :min="0"
              :max="100"
              :formatter="value => `${value}%`"
              :parser="value => value.replace('%', '')"
              size="large"
              @change="comPuteProfit"
            />
            <ArrowRightOutlined
              :style="{
                fontSize: '24px',
                marginLeft: '15px',
                marginRight: '15px',
              }"
            />
            <div style="display: inline-block">
              <a-input
                v-model:value="valueProfitRes"
                size="large"
                disabled
                style="width: 50%"
              />
            </div>
          </div>
          <div>
            手续费：
            <a-input-number
              v-model:value="valueService"
              :min="0"
              :max="100"
              :formatter="value => `${value}%`"
              :parser="value => value.replace('%', '')"
              size="large"
              @change="comPuteProfit"
            />
            <ArrowRightOutlined
              :style="{
                fontSize: '24px',
                marginLeft: '15px',
                marginRight: '15px',
              }"
            />
            <div style="display: inline-block">
              <a-input
                v-model:value="valueInput"
                size="large"
                disabled
                style="width: 50%"
              />
            </div>
          </div>

          <div class="btn">
            <a-button type="primary" size="large" @click="computeRes"
              >计算结果</a-button
            >
            <a-button type="primary" size="large" @click="queryRecord"
              >查询记录</a-button
            >
          </div>
        </div>

        <div class="result">
          <span class="title-res">本次计算结果：</span>
          <div>
            <span>利润：</span>
            <span>{{ profitResult }}</span>
          </div>
          <div>
            <span>利润率：</span>
            <span>{{ profitRate }}%</span>
          </div>
        </div>
        <div class="image">
          <h2>创作不易，欢迎打赏</h2>
          <div class="imgContainer">
            <img src="./../assets/zfb.jpg" />
          </div>
        </div>
        <a-drawer
          title="历史记录"
          placement="right"
          :closable="true"
          v-model:visible="visible"
          destroyOnClose
          width="800"
          :scroll="{ y: `calc(100vh - 400)` }"
        >
          <a-table
            :columns="columns"
            :data-source="dataVal"
            bordered
            :pagination="false"
          >
            <template #name="{ text }">
              <a>{{ text }}</a>
            </template>
          </a-table>
        </a-drawer>
      </div>
    </a-card>
  </div>
</template>
<script lang="ts">
import { defineComponent, reactive, ref, toRefs } from "vue";
import { ArrowRightOutlined } from "@ant-design/icons-vue";
interface ComputeData {
  valueCost: number;
  valuePrice: number;
  valueService: number;
  valueProfitInitial: number;
  valueInput: number;
  valueProfitRes: number;
}
export default defineComponent({
  name: "Compute",
  components: {
    ArrowRightOutlined,
  },
  setup(props) {
    const dataNum: ComputeData = reactive({
      valueCost: 0,
      valuePrice: 0,
      valueService: 0,
      valueProfitInitial: 0,
      valueInput: 0,
      valueProfitRes: 0,
    });
    const profitResult = ref<number>(0);
    const profitRate = ref<number>(0);
    const visible = ref<boolean>(false);
    const queryRecord = () => {
      visible.value = true;
    };

    const comPuteProfit = () => {
      const valueS = dataNum.valueService / 100;
      dataNum.valueInput = Number((dataNum.valuePrice * valueS).toFixed(2));
      const valueP = dataNum.valueProfitInitial / 100;
      dataNum.valueProfitRes = Number((dataNum.valuePrice * valueP).toFixed(2));
    };

    const computeRes = () => {
      profitResult.value = Number(
        (
          dataNum.valuePrice -
          dataNum.valueProfitRes -
          dataNum.valueInput -
          dataNum.valueCost
        ).toFixed(2)
      );
      profitRate.value = Number(
        ((profitResult.value / dataNum.valuePrice) * 100).toFixed(2)
      );
      //记录历史
      const key = String(dataVal.length + 1);
      const time = new Date().toLocaleString();
      const {
        valueCost,
        valuePrice,
        valueService,
        valueProfitInitial,
        valueInput,
        valueProfitRes,
      } = dataNum;
      const dataObj = {
        key,
        time,
        cost: valueCost,
        price: valuePrice,
        commission: `${valueProfitRes}（${valueProfitInitial}%）`,
        service: `${valueInput}（${valueService}%）`,
        profit: `${profitResult.value}（${profitRate.value}%）`,
        valueProfitInitial,
        valueService,
      };
      if (dataVal.length < 1) {
        dataVal.push(dataObj);
      } else {
        const { cost, price, valueProfitInitial, valueService } =
          dataVal[dataVal.length - 1];
        if (
          dataObj.cost !== cost ||
          dataObj.price !== price ||
          dataObj.valueProfitInitial !== valueProfitInitial ||
          dataObj.valueService !== valueService
        ) {
          dataVal.push(dataObj);
        }
      }
    };
    const columns = [
      {
        title: "时间",
        dataIndex: "time",
      },
      {
        title: "成本",
        className: "column-money",
        dataIndex: "cost",
      },
      {
        title: "售价",
        className: "column-money",
        dataIndex: "price",
      },
      {
        title: "佣金",
        className: "column-money",
        dataIndex: "commission",
      },
      {
        title: "手续费",
        className: "column-money",
        dataIndex: "service",
      },
      {
        title: "利润",
        className: "column-money",
        dataIndex: "profit",
      },
    ];

    const dataVal: any[] = [];

    return {
      ...toRefs(dataNum),
      queryRecord,
      visible,
      columns,
      comPuteProfit,
      profitResult,
      computeRes,
      profitRate,
      dataVal,
    };
  },
});
</script>
<style lang="less" scoped>
.numberContainer {
  display: flex;
  //justify-content: space-between;
  width: 100%;
  height: 100%;
  & > .input {
    width: 40%;
    & > div {
      padding: 20px 0px;
    }
  }

  & > .result {
    display: flex;
    flex-direction: column;
    width: 30%;
    & > div {
      display: flex;
      align-items: center;
      height: 50%;
      span {
        font-size: 32px;
      }
      &:last-child {
        align-items: flex-start;
      }
    }
  }

  & > .image {
    width: 30%;
    text-align: center;
    img {
      width: 100%;
      height: 400px;
    }
  }
  th.column-money,
  td.column-money {
    text-align: right !important;
  }
}
.btn {
  padding: 20px 0px;
  button {
    margin: 0px 15px;
  }
}
</style>
